@tailwind base;
@tailwind components;
@tailwind utilities;

#root{
    font-family: 'Outfit', sans-serif;
}
.btn:hover{
    @apply text-yellow-400 border-b-[1px] border-yellow-400 transition-all
}

/* Skills buttons styling */
    #skillContainer .skillLi{
        @apply px-2 py-1 shadow-md m-1 transition-all
}
#skillContainer .skillLi:hover{
    @apply cursor-default -translate-y-1 
}

/* Project component styling */

#bg-div{
    height: 250px;
    width: 100vw;
    background-image: url('../public/laptop-animation.gif');
    background-repeat: no-repeat;
    background-size: 400px;
    background-position: center center;
    
}